<template>
    <div>
        <!--头部区域-->
        <el-card class="operate-top-card" shadow="never">
             <i class="el-icon-s-order" style="font-size: 18px; margin-right: 3px;"></i>
             <span>商品分类列表</span>
            <el-button class="btn-add" size="mini" @click="$router.push('/pm/productCateAdd')">添加</el-button>
        </el-card>
        <!--内容区域-->
        <div class="table-container">
            <el-table
                :data="listData"
                v-loading="listLoading"
                border
                style="width: 100%"
            >
                <el-table-column label="编号" width="100" align="center">
                    <template slot-scope="scope">{{(scope.$index + 1) + ((pageNum-1)*pageSize)}}</template>
                </el-table-column>
                <el-table-column label="分类名称"  width="100" align="center">
                    <template slot-scope="scope">{{scope.row.name}}</template>
                </el-table-column>
                <el-table-column label="级别"  width="100" align="center">
                    <template slot-scope="scope">{{scope.row.parent_id | levelFilter}}</template>
                </el-table-column>
                <el-table-column label="数量单位"  width="100" align="center">
                    <template slot-scope="scope">{{scope.row.product_unit}}</template>
                </el-table-column>
                <el-table-column label="移动端显示"  width="100" align="center">
                    <template slot-scope="scope">
                        <el-switch
                            @change="handleNavStatusChange(scope.$index, scope.row)"
                            v-model="scope.row.nav_status"
                            :active-value="1"
                            :inactive-value="0">
                        </el-switch>
                    </template>
                </el-table-column>
                <el-table-column label="是否启用"  width="100" align="center">
                    <template slot-scope="scope">
                        <el-switch
                            @change="handleShowStatusChange(scope.$index, scope.row)"
                            v-model="scope.row.show_status"
                            :active-value="1"
                            :inactive-value="0">
                        </el-switch>
                    </template>
                </el-table-column>
                <el-table-column label="排序" align="center">
                    <template slot-scope="scope">{{scope.row.sort}}</template>
                </el-table-column>
                <el-table-column label="设置" align="center" width="200">
                    <template slot-scope="scope">
                        <el-button
                            size="mini"
                            :disabled="scope.row.parent_id | disableNextLevel"
                            @click="handleShowNextLevel(scope.$index, scope.row)"
                        >
                            查看下一级
                        </el-button>
                    </template>
                </el-table-column>
                <el-table-column label="操作" align="center" width="200">
                    <template slot-scope="scope">
                        <el-button
                            size="mini"
                            @click="handleUpdate(scope.$index, scope.row)"
                        >编辑</el-button>
                        <el-button
                            size="mini"
                            type="danger"
                            @click="handleDelete(scope.$index, scope.row)"
                        >
                            删除
                        </el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>
        <!--分页-->
        <div class="pagination-container">
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="pageNum"
                :page-sizes="[5, 10, 15]"
                :page-size="pageSize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="total">
            </el-pagination>
        </div>
    </div>
</template>

<script>
    import {getCategory, updateCategory, delCategory} from './../../../api/categoryApi'
    export default {
        name: "index",
        data(){
            return {
                listData: null,
                listLoading: false,
                pageNum: 1,
                pageSize: 5,
                parentId: 0,
                total: 0
            }
        },
        created(){
            this.resetParentId();
            console.log(this.parentId);
            this.getList();
        },
        watch: {
           $route(route){
               // console.log(route);
               this.resetParentId();
               this.getList();
           }
        },
        methods: {
            // 0. 重新设置父id
            resetParentId(){
               this.pageNum = 1;
               if(this.$route.query.parentId !== undefined){
                   this.parentId = this.$route.query.parentId;
               }else {
                   this.parentId = 0;
               }
            },
            // 1. 改变每页显示的条数
            handleSizeChange(val) {
                console.log(`每页 ${val} 条`);
                this.pageNum = 1;
                this.pageSize = val;
                // 重新加载数据
                this.getList();
            },
            // 2. 当前页码发生改变
            handleCurrentChange(val) {
                console.log(`当前页: ${val}`);
                this.pageNum = val;
                // 重新加载数据
                this.getList();
            },
            // 3. 获取列表数据
            getList(){
                this.listLoading = true;
                // 请求网络数据
                getCategory(this.parentId, this.pageNum, this.pageSize).then((response)=>{
                    console.log(response);
                    if(response.status === 1){
                        this.listLoading = false;
                        this.listData = response.data.category_list;
                        this.total = response.data.category_count;
                    }
                });
            },
            // 4. 移动端是否显示
            handleNavStatusChange(index, row){
                updateCategory({id: row.id, nav_status: row.nav_status}).then((response)=>{
                    console.log(response);
                    if(response.status === 1){
                         this.$message({
                             message: response.msg,
                             type: 'success',
                             duration: 1000
                         });
                     }else {
                         this.$message({
                             message: response.msg,
                             type: 'error',
                             duration: 1000
                         });
                     }
                });
            },
            // 5. 是否启用该分类
            handleShowStatusChange(index, row){
                updateCategory({id: row.id, show_status: row.show_status}).then((response)=>{
                    console.log(response);
                    if(response.status === 1){
                        this.$message({
                            message: response.msg,
                            type: 'success',
                            duration: 1000
                        });
                    }else {
                        this.$message({
                            message: response.msg,
                            type: 'error',
                            duration: 1000
                        });
                    }
                });
            },
            // 6. 查看下一级
            handleShowNextLevel(index, row){
                this.$router.push({path: '/pm/productCategory', query: {parentId: row.id}})
            },
            // 7. 删除一条记录
            handleDelete(index, row){
                this.$confirm('确定要删除该分类及其子分类, 是否继续?', '小撩温馨提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    delCategory(row.id).then(response => {
                        if(response.status === 1){
                            this.$message({
                                type: 'success',
                                message: '删除成功!'
                            });
                            this.getList();
                        }else {
                            this.$message({
                                type: 'error',
                                message: '删除失败!'
                            });
                        }
                    });
                });
            },
            // 8. 编辑
            handleUpdate(index, row){
                this.$router.push({path: '/pm/productCateUpdate', query: {id: row.id}})
            }
        },
        filters: {
            levelFilter(value){
                if(value === 0){
                    return '一级'
                }else {
                    return '二级'
                }
            },
            disableNextLevel(value){
                return value !== 0;
            }
        }
    }
</script>

<style scoped lang="less">

</style>
